.foot-container {
	//position: absolute;
	//bottom: 0% ;
	display: flex;
	width: 100%;
	height: 209px;
	margin-top: 5%;
	background-color: $footer-color;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	&__text {
		left: 32.15%;
		right: 32.22%;
		top: 64.11%;
		bottom: 13.88%;
		font-size: 12px;
		font-style: normal;
		font-weight: 500;
		line-height: 142.6%;
		display: flex;
		align-items: flex-end;
		justify-content: center;
		color: $tertiary-color;
		width: 100%;
		margin: 4% 0 3% 0;

		@media screen and (min-width: $breakpoint-tablet) {
			font-size: 24px;
		}
	}

	img {
		margin-top: 2%;
		left: 45.76%;
		right: 45.76%;
		top: 31.58%;
		bottom: 49.55%;
		width: 122px;
		height: 40px;
	}
}

