.errorContainer {
	display: flex;
	color: #ff6060;
	flex-direction: column;
	align-items: center;
	margin: 22px 6%;
}

.errorNumber {
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 96px;
	font-style: normal;
	font-weight: 700;
	line-height: 142.6%;
	margin: 12rem 0 1rem 0;

	@media screen and (min-width: $breakpoint-tablet) {
		font-size: 288px;
		font-style: normal;
		font-weight: 700;
		line-height: 142.6%;
		margin: 0;
	}
}

.errorText {
	display: flex;
	align-items: flex-end;
	justify-content: center;
	text-align: center;
	font-size: 18px;
	font-style: normal;
	font-weight: 500;
	line-height: 142.6%;
	margin: 0 11%;

	@media screen and (min-width: $breakpoint-tablet) {
		font-size: 36px;
		font-style: normal;
		font-weight: 500;
		line-height: 142.6%;
		margin: 0;
	}
}

.errorLinkHome {
	display: flex;
	align-items: flex-end;
	justify-content: center;
	text-decoration: none;
	color: #ff6060;
	font-size: 14px;
	font-weight: 500;
	margin-top: 9rem;

	@media screen and (min-width: $breakpoint-tablet) {
		font-size: 18px;
		font-style: normal;
		font-weight: 500;
		line-height: 142.6%;
	}

	&:hover {
		text-decoration-line: underline;
	}
}

